export default {
	data() {
		return {
			isSing: false,
			dailyPracticeList: [],
			taskListList: [],
			progress: '',
			studyCount: '',
			checkInStatus: '',
			backwordPlanId:'',
			completedOfToday:''
		}
	},
	methods: {
		// 查看排行跳转
		viewRanking(taskId, allWordCount) {
			uni.navigateTo({
				url: 'wordTasks/viewRanking/index?&taskId=' + taskId + "&allWordCount=" + allWordCount
			})
		},
		// 查看全部任务跳转
		viewAllTasks() {
			uni.navigateTo({
				url: 'wordTasks/index'
			})
		},
		// 修改计划跳转
		updatePlan() {
			uni.navigateTo({
				url: 'revisionPlan/index'
			})
		},
		// 去学习跳转
		toStudy(wordId, taskId,allWordCount) {
			uni.navigateTo({
				url: 'wordTasks/wordTaskLearning/study/index?wordId=' + wordId + "&taskId=" + taskId+'&taskType=0'+'&allWordCount='+allWordCount
			})
		},
		// 查看排行跳转
		toRanking(taskId) {
			console.log(taskId, 99)
			uni.navigateTo({
				url: 'viewRanking/index?&taskId=' + taskId
			})
		},
		// 签到页面跳转
		toSignIn() {
			uni.navigateTo({
				url: 'cardCalendar/index?checkInStatus=' + this.checkInStatus+'&completedOfToday='+this.completedOfToday
			})
		},
		// 获取每日一练数据
		dailyPractice() {
			this.$minApi.dailyPractice({}).then(data => {
				console.log(data)
				this.dailyPracticeList = data.data
				this.progress = data.data.completeCount / data.data.thesaurusCount * 100
				this.studyCount = data.data.studyCount
				this.reviewCount = data.data.reviewCount
				this.checkInStatus = data.data.checkInStatus
				this.backwordPlanId=data.data.backwordPlanId
				this.completedOfToday=data.data.completedOfToday
				// console.log(this.progress)
			})
		},
		// 每日一练----开始学习
		study() {
			console.log(this.studyCount)
			if (this.studyCount <= 0) {
				uni.showToast({
					title: '当前无学习计划单词数',
					icon: 'none'
				})
			} else {
				this.$minApi.study({
					wordCount: this.studyCount
				}).then(data => {
					console.log(data)
					uni.navigateTo({
						url: 'wordTasks/wordTaskLearning/study/index?wordId=' + data.data[0].wordId+'&taskType=1'+'&studyType=1'+'&backwordPlanId='+this.backwordPlanId+'&allWordCount='+this.dailyPracticeList.studyCount
					})
				})
			}

		},
		// 每日一练----复习
		review() {
			if (this.reviewCount <= 0) {
				uni.showToast({
					title: '当前无复习计划单词数',
					icon: 'none'
				})
			} else {
				this.$minApi.review({
					wordCount: this.reviewCount
				}).then(data => {
					console.log(data)
					uni.navigateTo({
						url: 'wordTasks/wordTaskLearning/study/index?wordId=' + data.data[0].wordId+'&taskType=1'+'&studyType=0'+'&backwordPlanId='+this.backwordPlanId+'&allWordCount='+this.dailyPracticeList.reviewCount
					})
				})
			}

		},
		// 获取个人任务列表
		taskList() {
			this.$minApi.taskList({
				page: 1,
				pageSize: 2
			}).then(data => {
				console.log(data)
				this.taskListList = data.data
			})
		}
	},
	onShow() {
		this.dailyPractice()
		this.taskList()
		
	}
}
